































import Vue from 'vue';

export default Vue.extend({
    name: 'adm',
    data() {
        return {
            router: this.$router
        }
    },
    created() {

    },

    methods: {
        routeToList(param: string): void {
            this.router.push({
                // path: `/words/wordsList/${param}`,
                name: 'wordsList',
                params: {id: param}
            });
        }
    }
})
    

